body {
  font-family: Arial, sans-serif;
}

.modal-backdrop {
  z-index: 999;
  background-color: #0009;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  z-index: 1000;
  background: #fff;
  border-radius: 10px;
  width: 300px;
  padding: 20px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 5px 15px #0000004d;
}

.modal-content input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  display: block;
}

.modal-buttons {
  justify-content: space-between;
  display: flex;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}
